import React from 'react'
import { Form, Row, Col } from 'antd'
import { UserOutlined, LockOutlined } from '@ant-design/icons'
import Button from '@/components/button'
import envStore from '@/constants/env'
// import { rules } from '@/utils/form'
import useLogin from '@/hooks/useLogin'
import history from '@/utils/history'
import useVisible from '@/hooks/useVisible'
import { LoginWrapper, LoginForm, Input, Checkbox } from './components'
import Result from './result'

type FormValues = {
  account: string
  password: string
}

export default function Login() {
  const [form] = Form.useForm()

  const { login, error } = useLogin({
    onError: () => {
      handleResultVisible()
    },
  })

  const [resultVisible, handleResultVisible] = useVisible()

  const handleLogin = () => {
    form.validateFields().then((values: any) => login(values))
  }

  const handleForgetPass = () => {
    history.push('/forget-password')
  }

  return (
    <LoginWrapper>
      {resultVisible && <Result {...error} onClose={handleResultVisible} />}
      {!resultVisible && (
        <LoginForm>
          <img
            src={`${envStore.publicUrl}/images/logo@3x.png`}
            alt="高雄榮民總醫院"
            width={230}
            className="mb-3 bg-white rounded-full"
          />
          <h1 className="mb-16 text-white text-3xl text-center">
            <div>高雄榮民總醫院</div>數位病理平台
          </h1>
          <Form form={form}>
            <Form.Item
              noStyle
              name="account"
              // rules={[rules.required]}
            >
              <Input
                size="large"
                placeholder="帳號"
                className="mb-6"
                prefix={<UserOutlined />}
                css={{ height: 48 }}
              />
            </Form.Item>
            <Form.Item
              noStyle
              name="password"
              // rules={[rules.required]}
            >
              <Input
                size="large"
                placeholder="密碼"
                className="mb-6"
                prefix={<LockOutlined />}
                css={{ height: 48 }}
              />
            </Form.Item>
            <Row className="mb-10">
              <Col span={24} className="flex justify-between items-center">
                <Form.Item noStyle>
                  <div
                    className="text-white cursor-pointer text-lg"
                    onClick={handleForgetPass}>
                    忘記密碼
                  </div>
                </Form.Item>
                <Form.Item noStyle name="rememberMe">
                  <Checkbox className="flex justify-end items-center text-white text-lg">
                    記住我
                  </Checkbox>
                </Form.Item>
              </Col>
            </Row>
            <Button
              block
              size="large"
              onClick={handleLogin}
              className="text-white text-xl hover--none"
              css={{
                height: 48,
                letterSpacing: 6,
                background: 'rgba(5,124,147,1)',
                borderColor: 'rgba(5,124,147,1)',
              }}>
              登入
            </Button>
          </Form>
        </LoginForm>
      )}
    </LoginWrapper>
  )
}
